<template>
    <div>
        <AppHeader>电台</AppHeader>
        <div class="banner">
            <Slide :bannerImg="bannerImg" :inv="invTime"></Slide>
        </div>
        
    </div>
</template>

<script>
import AppHeader from '../user/component/AppHeader.vue'
import Slide from '../find/component/Slide.vue'
export default {
    components:{
        AppHeader,
        Slide
    },
    data(){
        return{
            invTime:4000,
            bannerImg:[]
        }
    },
   async created(){
       const {data:res}=await this.$http.get('/dj/banner')
      
       res.data.forEach(item => {
           this.bannerImg.push(item.pic)
       });
       console.log(this.bannerImg)
    }
}
</script>

<style scoped>
.banner{
    width: 96%;
    margin-left: 2%;
    margin-top:20px;
}
</style>