<template>
<!-- 轮播图组件 -->
  <div class="banner">
    <div class="slide-show" @mouseover="clearInv" @mouseout="runInv">
      <div class="slide-img">
        <a>
            <transition name="slide-trans">
              
                <img v-if="isShow" :src="bannerImg[currentIndex]" @click="toPlayPage">
            
          </transition>
          <transition name="slide-trans-old">
            <img v-if="!isShow" :src="bannerImg[currentIndex]">
          </transition>
          
        </a>
      </div>
     <!-- 底部圆点 -->
      <ul class="slide-pages">
        <li v-for="(item, index) in bannerImg" :key="index" >
          <a :class="{on: index === currentIndex}" class="point"></a>
        </li>
      </ul>
      <!-- 左右箭头 -->
      <i class="arrow left-arrow el-icon-arrow-left" @click="goto(prevIndex)" @mouseover="clearInv" @mouseout="runInv"></i>
    <i class="arrow right-arrow el-icon-arrow-right" @click="goto(nextIndex)" @mouseover="clearInv" @mouseout="runInv"></i>
    </div>
    <!-- 左右滑动arrow -->
    
  </div>
</template>

<script>
export default {
  props: {
    
    inv: {
      type: Number,
      default: 1000
    },
    bannerImg:{
      type:Array,
      default:null
    }
  },
  data () {
    return {
      currentIndex: 0,
      isShow: true,
      invId:0,
     
     
    }
  },
  async created(){
   
     
    
  },
  computed: {
    prevIndex () {
      if (this.currentIndex === 0) {
        return this.bannerImg.length - 1
      }
      else {
        return this.currentIndex - 1
      } 
    },
    nextIndex () {
      if (this.currentIndex === this.bannerImg.length - 1) {
        return 0
      }
      else {
        return this.currentIndex + 1
      }
    }
  },
  methods: {
    goto (index) {
      this.isShow = false
      setTimeout(() => {
        this.isShow = true
        this.currentIndex = index 
      }, 10)
    },
    runInv () {
      this.invId = setInterval(() => {
        this.goto(this.nextIndex)
      }, this.inv)
    },
    clearInv () {
      clearInterval(this.invId)
    },
    toPlayPage(){
      console.log(this.currentIndex)
    }
  
  },
  mounted () {
    this.runInv();
  }
}
</script>

<style lang='less' scoped>
.slide-trans-enter-active {
  transition:  1s;
}
.slide-trans-enter {
  transform: translateX(115vw);
}
.slide-trans-old-leave-active {
  transition:  1s;
  transform: translateX(-124.4vw);
}
.banner{
  width: 100vw;
}
.slide-show {
  position: relative;
  margin: 15px 15px 15px 0;
  width: 100%;
  height: 160px;
  overflow: hidden;
}

.slide-img {
  width: 100%;
  height: 100%;
}
.slide-img img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.slide-pages {
  height: 20px;
  line-height: 20px;
  position: absolute;
 left:50%;
  transform: translateX(-50%);
  bottom: 5px;
  li{
    display: inline-block;
  }
  .point{
    display: inline-block;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    margin:0 5px;
    background: rgba(13, 13, 13, 0.6);
  }
}


.slide-pages li .on {
  text-decoration: underline;
  background-color: #fff;
  opacity: 0.5;
}
.left-arrow{
  position: absolute;
  font-size: 30px;
  color: #ccc;
  opacity: .6;
  left: 0;
 top: 50%;
 transform: translateY(-50%);
 
 
}
.right-arrow{
  position: absolute;
  font-size: 30px;
  color: #ccc;
  opacity: .6;
  right: 0;
 top: 50%;
 transform: translateY(-50%);
}
.imglist{
      position: absolute;
      li img{
          width: 100%;
          float: left;
      }
    .navDiv{
        position: absolute;
        bottom: 15px;			
        li a{
            float: left;
            width: 15px;
            height: 15px;
            background-color: red;
            margin: 0 5px;
				/*设置透明*/
				opacity: 0.5;
				/*兼容IE8透明*/
				filter: alpha(opacity=50);
        }
    }
  }


</style>
